html, body {
    margin: 0;
    padding: 0;
}

.MainForm {
    padding: 10pt 100pt;
}

.headerStyle {
    width: 100%;
    height: 100%;
    align-items: center;
    text-align: center;
    padding: 30pt;
    margin: 0;
    background-position: center;
    background-size: cover;
}

.loader {
    border: 5px solid #f3f3f3; /* Light grey */
    border-top: 5px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: inline-block;
    animation: spin 2s linear infinite;
    margin: 0 13px;
}

.container {
    margin: 0;
}

#enviar {
    height: 40px;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media only screen and (max-width: 660px) {
    .MainForm {
        padding: 10%;
    }

    .loader {
        display: none;
    }

    #enviar {
        width: 100%;
    }
}
  